*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --black-color: black;
    --light-font:  #EADFE8;
    --purple: #926A8E;
}
.side-div-shadow-contact{
    position: absolute;
    z-index: 1;
    width: 25vw;
    opacity: 0.3;
    filter: blur(120px);
    border-radius: 100%;
    
    height:  25vw;
    background-color: rgb(189, 107, 0);
    left: -200px;
    top: auto;
    right: auto;
    bottom:  100px;
}

.contact-main-container{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3em;
    padding-bottom: 10em;
}

.contact-heading{
    color: #EADFE8;
    display: flex;
    width: 70%;
    align-items: flex-end;
    flex-direction: column;
    justify-content: flex-end;
}
.contact-heading h1{
    color: var(--light-font);
    font-size: 4rem;
    font-weight: 400;
}
.contact-heading >p{
    color: var(--purple);
    font-size: 2rem;
    font-weight: 600;
    position: relative;
}
.contact-heading >p::before{
    content: "";
    top: 50%;
    position: absolute;
    width: 100%;
    background-color: var(--purple);
    height: 2px;
    right: 105%;

}

.contact-main-wrapper{
    width: 70%;
    position: relative;
    display: flex;
    
    justify-content: space-between;
    align-items: center;
    
}
.contact-main-wrapper::before {
   
    position: absolute;
    content: "";
    background-color: var(--light-font);
    width: 50%;
    height: 1px;
    bottom: 0;
    right: 0;
    
    
}
.contact-main-wrapper::after {
   
    position: absolute;
    content: "";
    background-color: var(--light-font);
    width: 1px;
    height: 100%;
    right: 0;
    bottom: 0;
    
    
}

/* contact form */

.Contact_form{
    display: flex;
    justify-content: center;
    gap: 2em;
    color: var(--light-font);
    flex-direction: column;
    width: 50%;
    height: 100%;
}
.Contact_form div{
    display: flex;
    justify-content: center;

    flex-direction: column;
}
.Contact_form input{
    border: none;
    background-color: rgba(255, 255, 255, 0);
    border-bottom: 1px rgb(255, 255, 255) solid;
    padding: 1em 0px 0em 0px;
    font-size: 1.5em;
    color: var(--light-font);
}   

.Contact_form input:focus {
    outline: none;
  }
.Contact_form label{
    font-size: 1.5em;
    
}
.Contact_form h1{
    font-size: 3.5em;
    color: var(--purple);
}
.Contact_form form {
    display: flex;
    justify-content: center;
 
    flex-direction: column;
    width: 100%;
    height: 100%;
    gap: 2em;

}
.Contact_form h1 span{
    color: var(--light-font);
    font-size: 0.85em;
    font-weight: 400;
}
.Contact_form button{
    padding: 0.5em;
    font-size: 1.3em;
    border: 1px rgb(255, 255, 255) solid;
    position: relative;
    background-color: rgba(255, 255, 255, 0);
    overflow: hidden;
    transition: color 0.5s ease;
    color: rgb(255, 255, 255);
    z-index: 1;
}
.Contact_form button:hover{
    cursor: pointer;
    color: rgb(0, 0, 0);
}
.Contact_form button:hover::before{
    left: 0%;
}
.Contact_form button::before{
    content: "";
    position: absolute;
    width: 100%;
    top: 0;
    left: -100%;
    transition: left 0.5s ease;
    height: 100%;
    background-color: rgb(255, 255, 255);
    z-index: -1;
}


/* contact img  */

.contact-img-div{
    width: 30%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact-img{
    background-image: url("../utils/img/contact-img.jpg");
    background-position: -100px;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 60%;
}