*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --black-color: black;
    --light-font:  #EADFE8;

    --purple: #926A8E;

    --step--2: clamp(0.78rem, calc(0.52rem + 1.28vw), 1.52rem);
    --step--1: clamp(0.94rem, calc(0.60rem + 1.67vw), 1.90rem);
    --step-0: clamp(1.13rem, calc(0.69rem + 2.17vw), 2.38rem);
    --step-1: clamp(1.35rem, calc(0.79rem + 2.82vw), 2.97rem);
    --step-2: clamp(1.62rem, calc(0.89rem + 3.64vw), 3.71rem);
    --step-3: clamp(1.94rem, calc(1.01rem + 4.69vw), 4.64rem);
    --step-4: clamp(2.33rem, calc(1.13rem + 6.03vw), 5.80rem);
    --step-5: clamp(2.80rem, calc(1.25rem + 7.74vw), 7.25rem);
    --step-6: clamp(3.36rem, calc(1.38rem + 9.91vw), 9.06rem);
    --step-7: clamp(4.03rem, calc(1.49rem + 12.68vw), 11.33rem);
    --step-8: clamp(4.84rem, calc(1.60rem + 16.21vw), 14.16rem);
}
.Typewriter{
    
    color: var(--purple);
    display: inline;
}
.profile-main-container{
    width: 100%;
    
    height: 100vh;
    display: flex;
    justify-content: center;
    
}
.profile-wrapper{
    width: 80%;
   
    display: flex;
}
.profile-description{
    width: 60%;
    display: flex;
    z-index: 1;
    position: relative;
    flex-direction: column;
    justify-content: center;
    gap: 2em;
}
.profile-description p{
    color: var(--purple);
    font-size: var(--step--1);
    position: relative;
    font-weight: 400;
}
.profile-description>div>div{
    display: flex;
    justify-content: flex-end;

}
.profile-description p::before{

    content: "";
    position: absolute;
    top: 50%;
    right: 5em;
    width: 17vw;
    height: 2px;
    background-color: #926A8E;

}
.profile-description h1{
    color: var(--light-font);
    font-size: var(--step-2);
    font-weight: 400;
}
.profile-image{
    width: 40%;
    background-repeat: no-repeat;
    background-position:  center ;
    background-size: cover;
    clip-path: polygon(47% 0, 100% 0, 100% 100%, 0% 100%);
    height: 100%;
    background-image: url('../utils/img/profile-img-temp.jpg');
}
.resume-btn{
    width: 14vw;
    font-size: var(--step--2);
    background-color:rgb(255, 255, 255 ,0);
    border: 1px solid white;
    box-shadow: none;
    color: white;
    font-weight: lighter;
    padding-top:0.4em;
    position: relative;
    padding-bottom:0.4em;
    overflow: hidden;
    transition: color 0.4s ease;
    
}
.resume-btn:hover{
    cursor: pointer;
    color: black;
  
}
.resume-btn:hover::before{
    left: 0;

    cursor: pointer;

}
.resume-btn::before{
    z-index: -1;
    width: 100%;
    height: 100%;
    position: absolute; 
    content: "";
    background-color: #ffffff;
    top: 0;
    left: -100%;
    transition: left 0.4s ease ;

}



.side-div-shadow{
    position: absolute;
    z-index: 1;
    width: 25vw;
    opacity: 0.3;
    filter: blur(120px);
    border-radius: 100%;
    
    height:  25vw;
    background-color: var(--purple);
    left: -200px;
    top: auto;
    right: auto;
    bottom:  -200px;
}

@media (max-width: 768px) {
   
    .profile-image{
       display: none;
       width: 0%;
    }
    .profile-description{
        width: 100%;
    }
  }