*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --black-color: black;
    --light-font:  #EADFE8;
    --purple: #926A8E;
    --step--2: clamp(0.78rem, calc(0.52rem + 1.28vw), 1.52rem);
    --step--1: clamp(0.94rem, calc(0.60rem + 1.67vw), 1.90rem);
    --step-0: clamp(1.13rem, calc(0.69rem + 2.17vw), 2.38rem);
    --step-1: clamp(1.35rem, calc(0.79rem + 2.82vw), 2.97rem);
    --step-2: clamp(1.62rem, calc(0.89rem + 3.64vw), 3.71rem);
    --step-3: clamp(1.94rem, calc(1.01rem + 4.69vw), 4.64rem);
    --step-4: clamp(2.33rem, calc(1.13rem + 6.03vw), 5.80rem);
    --step-5: clamp(2.80rem, calc(1.25rem + 7.74vw), 7.25rem);
    --step-6: clamp(3.36rem, calc(1.38rem + 9.91vw), 9.06rem);
    --step-7: clamp(4.03rem, calc(1.49rem + 12.68vw), 11.33rem);
    --step-8: clamp(4.84rem, calc(1.60rem + 16.21vw), 14.16rem);
}


.project-main-container{
    height: auto;
    width: 100%;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    margin-bottom: 10em;
    background-color:var(--black-color);
}
.project_wrapper{
    width: 80%;
    height: auto;
    color: var(--light-font);
}
.project_heading >h1{
    color: var(--light-font);
    font-size: 4rem;
    font-weight: 400;
    
}
.project_heading >p{
    color: var(--purple);
    font-size: var(--step--1);
    font-weight: 600;
    position: relative;
}
.project_heading >p::before{
    content: "";
    top: 50%;
    position: absolute;
    width: 17vw;
    background-color: var(--purple);
    height: 2px;
    left: 5em;

}

.project-main-div{
    width: 100%;
    height: auto;
    background-color: black;
    display: flex;
    flex-direction: column;
    gap: 5em;
    align-items: center;

}

/* project styling */

.project {
   

    width: 100%;
    height: 500px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    
}


.project::before {
   
    position: absolute;
    content: "";
    background-color: var(--light-font);
    width: 60%;
    height: 1px;
    bottom: 0;
    
    
}
.project::after {
   
    position: absolute;
    content: "";
    background-color: var(--light-font);
    width: 1px;
    height: 100%;
    left: 0;
    bottom: 0;
    
    
}
.project-1-img{
    background-image: url("../utils/img/project-1.png");
    width: 35em;
    height: 20em;
    background-repeat: no-repeat;
    background-position: left;
    background-size: cover;
    
    position: relative;
}

.project-details{
    width: 27em;
    height:auto;
    left: 30em;
    position: absolute;
 
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2em;
    align-items: flex-end;
}

.project-number{
    font-size: 1.5em;
    width: 100%;
    position: relative;
    text-align: end;
    color: var(--light-font);
}


.project-name{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: flex-end;
}
.project-name h1{
    font-size: var(--step--1);

}
.project-name p{
    font-size: 1em;

}
.project-description{
 
    background-color: #331b31;
    width: 100%;
    box-shadow: 0px 0px 4px 2px #522b4e;
    padding: 1.3em;
    line-height: 1.3em;
    
}
.project-description span{
    font-size: 1.4em;
}
.project-description p{
    width: 100%;
    display: flex;
    justify-content: flex-end;
}
.project-stack{
    display: flex;
    width: 100%;
    justify-content: flex-end;
    gap: 2em;
    

}

@media (max-width: 700px) {
    
    .project-1-img{
        width: 70vw;
    }
}

