*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --black-color: black;
    --light-font:  #EADFE8;
    --purple: #926A8E;
    --step--2: clamp(0.78rem, calc(0.52rem + 1.28vw), 1.52rem);
    --step--1: clamp(0.94rem, calc(0.60rem + 1.67vw), 1.90rem);
    --step-0: clamp(1.13rem, calc(0.69rem + 2.17vw), 2.38rem);
    --step-1: clamp(1.35rem, calc(0.79rem + 2.82vw), 2.97rem);
    --step-2: clamp(1.62rem, calc(0.89rem + 3.64vw), 3.71rem);
    --step-3: clamp(1.94rem, calc(1.01rem + 4.69vw), 4.64rem);
    --step-4: clamp(2.33rem, calc(1.13rem + 6.03vw), 5.80rem);
    --step-5: clamp(2.80rem, calc(1.25rem + 7.74vw), 7.25rem);
    --step-6: clamp(3.36rem, calc(1.38rem + 9.91vw), 9.06rem);
    --step-7: clamp(4.03rem, calc(1.49rem + 12.68vw), 11.33rem);
    --step-8: clamp(4.84rem, calc(1.60rem + 16.21vw), 14.16rem);
  
}


.main-menu-container{
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(0, 0, 0,0);
}

.menu-wrapper{
    width: 65%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
@media (max-width: 1476px){
    .menu-wrapper{
        width: 80%;
    }

}

.menu-links-div {
    position: relative;
    z-index: 1;
    font-size: var(--step-5);
    overflow: hidden;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
   
    color: rgb(0, 0, 0);
    height: 100%;
}
@media (max-width: 1000px){
    .menu-wrapper{
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }
    .menu-links-div{
        width: 80%;
        height: 60%;
    }

}
.menu-links-div>div{
    display: flex;
    z-index: 1;
    position: relative;
    align-items: center;
}
.menu-links:hover{
    cursor: pointer;
}

.menu-left-line:has(+ .menu-links:hover) {
    width: 20%;
}
.menu-left-line{
    transition: width 0.6s ease;
    width: 0%;
    height: 6px;
    background-color: black;
    
    


}
.menu-links{
    position: relative;
    left: 0;
}
.menu-div{
    border-right: 1px #d0d0d0 solid;
    background-color: var(--light-font);
}
.menu-div-1{
    position: absolute;
    left: 0;
    width: 20%;
    height: 0%;
    bottom: 0;

    background-color: var(--light-font);

}
.menu-contact-info{
    position: relative;
    overflow: hidden;
    z-index: 1;
    
}
.menu-contact-info div{
    left: 0;
    position: relative;

}
.menu-contact-info h1{
    font-size: var(--step-1);
}
.menu-contact-info p{
    font-size:var(--step--1);;
}

.menu-div-2{
    position: absolute;
    left: 20%;
    width: 20%;
    bottom: 0;
    height: 100%;
    background-color: var(--light-font);
}
.menu-div-3{
    position: absolute;
    left: 40%;
    width: 20%;
    bottom: 0;
    height: 100%;
    background-color: var(--light-font);
}
.menu-div-4{
    position: absolute;
    left: 60%;
    width: 20%;
    bottom: 0;
    height: 100%;
    background-color: var(--light-font);
}
.menu-div-5{
    position: absolute;
    left: 80%;
    width: 20%;
    bottom: 0;
    height: 100%;
    background-color: var(--light-font);
}